//Custom styles
//header
.page-header {
    background-color: #f6f6f6;
    height: 8rem;
    display: flex;
    align-items: center;
    width: 100%;

    @include mobile {
        height: auto;
    }
}

.page-nav {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;

    @include mobile {
        display: block;

        .nav-brand {
            display: block;
            margin: 20px 0 20px;
            text-align: center;
        }
    }
}

.nav-list {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    a {
        font-size: 14px;
        font-weight: var(--w-medium);
        color: var(--typo-body);
        text-transform: uppercase;
        text-decoration: none;
        margin-left: 3rem;

        &:nth-of-type(1) {
            margin-left: 0;
        }

        &:hover {
            color: var(--primary-color);
        }
    }

    .toggle-navbar {
        font-size: 20px;
        line-height: 1;
        display: none;

        @include tablet-pro {
            display: block;
        }
    }

    @include mobile {
        justify-content: center;
        margin: 10px 0 15px;

        a {
            font-size: 12px;
            margin-left: 2rem;
        }
    }
}

//sidebar
.main-content {
    display: flex;
    flex-wrap: wrap;
}

.sidebar-navigation {
    position: fixed;
    top: 120px;
    bottom: 40px;
    width: 350px;
    padding: 3rem;
    background-color: #f6f6f6;
    overflow-x: hidden;
    transition: all .5s;

    &.stick-to-top {
        top: 40px;

        @include tablet-pro {
            top: 0;
        }
    }

    @include tablet-pro {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        width: 300px;
        transform: translateX(-310px);
        transition: all .5s;
        z-index: 9999;
        box-shadow: 3px 0 10px rgba(#000, .1);
    }

    &.show {
        transform: translateX(0);
    }

    @include mobile {
        width: 260px;
    }
}

.doc-nav {
    padding-left: 0;
    list-style: none;

    >li {
        margin-bottom: 1.5rem;
    }

    ul {
        padding-left: 3rem;
        list-style: none;

    }

    ol {
        padding-left: 3rem;
    }

    a {
        display: block;
        font-size: 16px;
        line-height: 3rem;
        color: var(--typo-body);
        text-decoration: none;
        padding: 0;

        &.active,
        &:hover {
            color: var(--action-color);
        }
    }

}

//main content
.main {
    flex: 1;
    max-width: 100%;
    margin-left: 410px;
    padding: 0 0 30px 30px;

    @include tablet-pro {
        margin: 60px 0 0;
        padding: 0;
    }
}

.section-1,
.section-2 {
    height: 2000px;
}

input,
textarea {
    width: 100%;
    padding: 1rem 2rem;
    background: #fff;
    border: 2px solid #eee;
    font-size: 16px;
    line-height: 30px;
    color: var(--typo-body);
}

.input-group {
    position: relative;

    button {
        position: absolute;
        background: transparent;
        border: none;
        top: 0;
        right: 0;
        font-size: 16px;
        line-height: 34px;
        padding: 1.5rem 2rem;
        cursor: pointer;
        transition: all .5s;

        i {
            font-weight: var(--w-medium);
            color: var(--typo-body);
            transition: all .5s;
        }

        &:hover {
            i {
                color: var(--primary-color);
            }
        }
    }
}

.main {
    h3 {
        border-bottom: 1px solid #eee;
        padding-bottom: 1rem;
        margin-bottom: 3rem;
        margin-top: 5rem;
        transition: color .5s;
    }

    h4 {
        margin: 0 0 2rem;
    }

    p {
        margin-bottom: 2rem;
    }

    ul,
    ol {
        margin: 0 0 2rem;
    }

    img {
        margin-bottom: 3rem;
        box-shadow: 0 2px 10px rgba(0, 0, 0, .1);
    }

}

img {
    max-width: 100%;
    height: auto;
}

//footer
.page-footer {
    background-color: var(--typo-dark);
    padding: 19px 0;
    position: relative;

    p {
        color: #fff;
        margin: 0;
    }

    a {
        color: #fff;

        &:hover {
            color: var(--primary-color);
        }
    }
}

.nav {
    flex-direction: column;

    @include tablet-pro {
        display: block;
    }
}

.main-content {
    @include tablet-pro {
        padding-top: 1px;
        padding-bottom: 50px;
    }
}

section.active {
    h3 {
        color: var(--action-color);
    }
}

.sidebar-navigation ol {
    counter-reset: section;
    list-style-type: none;

    li a {
        &::before {
            counter-increment: section;
            content: counters(section, ".") ". ";
        }
    }
}

.back-to-top {
    position: fixed;
    bottom: -6rem;
    right: -6rem;
    display: block;
    width: 4rem;
    line-height: 4rem;
    background: var(--action-color);
    color: #fff;
    text-align: center;
    text-decoration: none;
    border-radius: var(--radius);
    opacity: 0;
    transform: scale(.3);
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.5);
    z-index: 9;
    transition: all .3s;

    &.show {
        bottom: 3rem;
        right: 3rem;
        opacity: .7;
        transform: scale(1);
    }

    &:hover {
        color: #fff;
        bottom: 3.2rem;
        opacity: 1;
    }
}

.nav-link {
    &:hover {
        text-decoration: underline;
    }
}

li {
    img {
        display: block;
        margin-top: 20px;
    }
}